.front {
    background-image: linear-gradient(
        to right bottom,
        rgba(180, 201, 176, 0.801),
        #42554f
      ),url("../../images/happy-nurse.jpg");
    background-size: cover;
    background-position: center;
    top: 0;
    position: absolute;
    width: auto;
    height: 80vh;
    position: relative;
}

.text-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
}

.primary {
    display: block;
    font-size: 60px;
    font-weight: 400;
    letter-spacing: 4px;
}

.secondary {
    display: block;
    font-size: 25px;
    font-weight: 700;
    letter-spacing: 7px;
    text-align: center;
} 